import React from 'react'
import moment from 'moment'
import 'moment/locale/he';
import { Link } from 'react-router-dom'

export default function LastExamsPreview(props) {

    const { exam } = props

    function getType(type) {
        switch (type) {
            case 'car':
                return ' '
            case 'bus':
                return ''
            case 'truck':
                return ''
            case 'tractor':
                return ''
            case 'motorcycle':
                return ''
            default:
                return ' ';
        }
    }

    return (
        <article className="exam flex align-center">
            <img src="../../assets/img/exam-icon.png" className="icon" alt="" />
            <span> : {exam.wrongCounter}</span>
            <span className={`status ${exam.wrongCounter > 4 ? 'failed' : 'passed'}`}> : {exam.wrongCounter > 4 ? '' : ''}</span>
            <span className="finished-at">{moment(exam.finishedAt).fromNow()}</span>
            <span className="exam-type"> : {getType(exam.type)}</span>
            <span><Link to={`/exam/${exam.type}/${exam._id}`}> </Link></span>
        </article>
    )
}
