import React from 'react'
import Option from './Option'
import { useSelector } from 'react-redux'

export default function OptionList() {
    const loggedInUser = useSelector(state => state.user.loggedInUser)
    return (
        <section className="options-container container flex justify-center wrap">
            {!loggedInUser && <Option title="    " className="login" />}
            <Option title=" " className="exam" />
            {loggedInUser && <Option title=" " className="last-exams" />}
            <Option title=" " className="study-material" />
        </section>
    )
}
