import React, { useState } from 'react'
import { useHistory, Link } from 'react-router-dom'

export default function FinishExam(props) {
    const [isWatchAfterFinish, setIsWatchAfterFinish] = useState(false)

    function onBackToExam() {
        setIsWatchAfterFinish(true)
    }

    const { exam } = props
    if (isWatchAfterFinish) return '';
    return (
        <>
            <div className={`screen ${isWatchAfterFinish ? 'no-display' : ''}`}></div>
            <section className={`modal-container ${isWatchAfterFinish ? 'no-display' : ''}`}>
                <header className="flex align-center">
                    <h2 className="title"> </h2>
                </header>
                <div className="content">
                    <div className="wrong-counter">
                        <h3 className="wroung-counter"> : <span className="bold">{exam.wrongCounter}</span></h3>
                        <h4 className="pass">
                            {exam.wrongCounter > 4 && '   ...'}
                            {exam.wrongCounter <= 4 && ' !   !'}
                        </h4>
                    </div>
                </div>
                <footer>
                    <Link to={`/exam/${props.currExamType}`} className="btn primary"> </Link>
                    <button onClick={onBackToExam} className="btn danger">  ?</button>
                </footer>
            </section>
        </>
    )
}
